import axios from '@/libs/axios'
import {loginUrl, logoutUrl} from './url';

export const login = ({username, password}) => {
  const data = {
    username,
    password
  };
  return axios.request({
    url: loginUrl,
    data,
    method: 'post',
  })
};

export const logout = () => {
  return axios.request({
    url: logoutUrl,
    method: 'post'
  })
};



